@import '../base/variables';
@import '../base/layout';

.short-news-panel {
  background: white;

  .short-news-panel__title-container {
    padding-bottom: 50px;

    text-align: left;

    @media @small-screen {
      text-align: center;
    }
  }

  .short-news-panel__title {
    display: inline-block;

    min-width: 210px;
    padding-bottom: 12px;

    font-family: @main-control-font;
    font-size: 24px;

    text-transform: uppercase;

    color: @text-color-1;
    border-bottom: 1px solid @text-color-1;
  }

  .short-news-panel__footer {
    text-align: center;
  }
}

.short-news {
  display: inline-block;

  box-sizing: border-box;
  width: 100%;
  padding-bottom: 50px;

  &:after {
    display: table;
    visibility: hidden;
    clear: both;

    content: '';
  }

  &:nth-child(even) {
    .short-news__content {
      float: right;

      padding-right: 10%;
      padding-left: 20px;

      text-align: left;

      @media @small-screen {
        padding: 10px;

        text-align: center;
      }
    }

    .short-news__image-container {
      text-align: left;

      @media @small-screen {
        text-align: center;
      }
    }
  }

  .short-news__content {
    float: left;

    box-sizing: border-box;
    width: 60%;
    padding-right: 20px;
    padding-left: 10%;

    text-align: right;

    @media @small-screen {
      width: 100%;
      padding: 10px;

      text-align: center;
    }
  }

  .short-news__content-title {
    display: block;

    padding-bottom: 20px;

    font-family: @main-control-font;
    font-size: 22px;
    font-weight: 400;
    line-height: 28px;

    text-transform: uppercase;

    color: @text-color-1;
  }

  .short-news__content-text {
    display: block;

    padding-bottom: 50px;

    font-family: @main-control-font;
    font-size: 14px;
    line-height: 18px;

    text-align: justify;

    color: @text-color-1;
  }

  .short-news__image-container {
    display: inline-block;

    box-sizing: border-box;
    width: 40%;

    @media @small-screen {
      width: 100%;
      max-height: none;
      margin: 0 auto;
      padding: 10px;

      text-align: center;
    }
  }

  .short-news__image {
    width: 100%;

    @media @small-screen {
      max-height: none;
    }
  }
}

