@import '../base/variables';
@import '../base/layout';

.achievements {
    background-color: @background-color-4;



    .achievements__title-container {
        padding-bottom: 50px;

        text-align: left;

        @media @small-screen {
            text-align: center;
        }
    }

    .achievements__title {
        display: inline-block;

        min-width: 200px;
        padding-bottom: 12px;

        font-family: @main-control-font;
        font-size: 24px;

        text-transform: uppercase;

        color: @text-color-1;
        border-bottom: 1px solid @text-color-1;
    }

    .achievements__list {
        display: flex;

        align-items: stretch;
        justify-content: center;
        flex-wrap: wrap;
    }
}

.achievement {
    position: relative;

    display: block;

    box-sizing: border-box;
    padding: 10px;

    .achievement__content {
        box-sizing: border-box;
        width: 210px;
    }

    .achievement__icon-container {
        width: 100%;
        padding-bottom: 25px;

        text-align: center;
    }

    .achievement__icon {
        font-size: 90px;

        color: @background-color-1;
    }

    .achievement__title-container {
        display: block;

        width: 100%;
        padding-bottom: 15px;

        text-align: center;
    }

    .achievement__title {
        font-family: @main-control-font;
        font-size: 22px;
        font-weight: bold;

        color: @text-color-2;
    }

    .achievement__description-container {
        width: 100%;

        text-align: center;
    }

    .achievement__description {
        display: inline-block;

        max-height: 100px;

        font-size: 14px;
        line-height: 16px;

        color: @text-color-1;
    }
}
