@import '../base/variables';

.button {
    display: inline-block;

    padding: 20px 50px;

    font-family: @main-control-font;
    font-size: 14px;
    font-weight: bold;

    cursor: pointer;
    text-transform: uppercase;

    color: @text-color-1;
    border: 1px solid @text-color-1;

    &.button--dark {
        color: @text-color-1;
        border: 3px solid @background-color-1;
    }

    &.button--light {
        color: @text-color-2;
        border: 3px solid @text-color-2;
    }

    &.button--warning {
        color: white;
        border: none;
        background-color: @text-color-3;
    }
}
